import React, { useEffect, useRef, useState } from "react"
import Layout from "../components/layout"
import ReactQuill, { Quill } from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import katex from "katex";
import 'katex/dist/katex.css';
import './../components/styles/quill.css';
import html2canvas from 'html2canvas'
import { Button, Empty, Input, Modal } from "antd";
import api from "../utils/api";
let ImageResize;
let _formulaHandler;
window.Quill = Quill
if (window.Quill) {
  ImageResize = require("quill-image-resize-module").default;
  Quill.register('modules/imageResize', ImageResize);
}
const modules = {
  toolbar: {
    container: [
      ['bold', 'italic', 'underline', 'strike'],
      ['blockquote', 'code-block'],
      [{ 'header': 1 }, { 'header': 2 }],               // custom button values
      [{ 'script': 'sub' }, { 'script': 'super' }],      // superscript/subscript
      [{ 'color': [] }],          // dropdown with defaults from theme
      [{ 'align': [] }],
      ['link'],
      ['image', 'formula']
    ],
    handlers: {
      image: () => api._imageHandler(quillRef),
      'formula': (value) => {
        if (value) {
          _formulaHandler()
        }
      }
    }
  },
  imageResize: true
}
//  ()
//  ()
//  ()
//  
// katex()
// katex()
const Exam = () => {
  if (!window.Quill) return
  const quillRef = useRef()
  const readRef = useRef()
  const [content, setContent] = useState('')
  const [visible, setVisible] = useState(true)
  const [latex, setLatex] = useState('')
  useEffect(() => {
    window.katex = katex
  }, [])
  _formulaHandler = () => {
    setVisible(true)
  }
  useEffect(() => {
    _changeLatex()
  }, [latex])
  const _changeLatex = () => {
    if (!latex) return
    const readQuill = readRef.current.editor
    const result = readQuill.insertEmbed(0, 'formula', latex);
    readQuill.setContents(result)
  }
  const _saveLatex = () => {
    html2canvas(document.querySelector("#read_latex .ql-editor")).then(canvas => {
      const oldCanvas = document.getElementById('read_canvas')
      if (oldCanvas) {
        oldCanvas.parentElement.removeChild(oldCanvas)
      }
      canvas.id = 'read_canvas'
      document.body.appendChild(canvas)

      const oldImage = document.getElementById('read_image')
      if (oldImage) {
        oldImage.parentElement.removeChild(oldImage)
      }
      var image = new Image();
      image.src = canvas.toDataURL("image/png");
      image.id = 'read_image'
      document.body.appendChild(image);
      const quill = quillRef.current.editor
      const range = quill.getSelection(true)
      if (range != null) {
        var index = range.index + range.length;
        quill.insertEmbed(index, 'image', image.src);
        quill.insertText(index + 1, ' ');
        quill.setSelection(index + 2);
        setLatex('')
        setVisible(false)
      }
    });
    return
    const quill = quillRef.current.editor
    const range = quill.getSelection(true)
    if (range != null) {
      var index = range.index + range.length;
      quill.insertEmbed(index, 'formula', latex);
      quill.insertText(index + 1, ' ');
      quill.setSelection(index + 2);
      setLatex('')
      setVisible(false)
    }
  }
  const renderFormula = () => {
    return <Modal
      title="Latex"
      visible={visible}
      onOk={_saveLatex}
      onCancel={() => setVisible(false)}
    >
      <Input
        value={latex}
        placeholder="latex"
        style={{ marginBottom: 10 }}
        onChange={(e) => setLatex(e.target.value)}></Input>
      {!latex ? <Empty
        description='latex'
        imageStyle={{ height: 50 }}></Empty> :
        <ReactQuill
          theme="bubble"
          ref={readRef}
          id='read_latex'
          readOnly
          modules={{ toolbar: null }}
          style={{
            margin: 'auto',
            width: 'fit-content',
            height: 'auto',
            maxWidth: '100%',
            padding: '0 !important'
          }}
          onChange={setContent}
        ></ReactQuill>}
    </Modal>
  }
  useEffect(() => {
    console.log(content)
  }, [content])
  return (
    <Layout title={'exam'}>
      {renderFormula()}
      <ReactQuill
        theme="snow"
        ref={quillRef}
        id="quill"
        style={{ height: 'calc(100vh - 202px)', marginTop: 15 }}
        // value={content}
        onChange={setContent}
        modules={modules}
      >
      </ReactQuill>
    </Layout >
  )
}

export default Exam